.stars {
  display: flex;

  &__star {
    background: url("../images/star.svg") no-repeat center;
    height: $starSize;
    width: $starSize;

    &:not(:first-child) {
      margin-left: 4px;
    }
  }

  &--4 &__star:nth-child(-n + 4) {
    background-image: url("../images/star-active.svg");
  }
}
